/* Onboarding Screen Styles */
.onboarding-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #000;
  color: #fff;
  overflow: hidden;
  transition: background 0.5s ease;
}

/* Background image layer */
.onboarding-bg {
  position: absolute;
  inset: 0;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  transform: scale(1.05);
}

/* Dark-to-light overlay for better text visibility */
.onboarding-overlay {
  position: absolute;
  inset: 0;
  /* Stronger white towards the bottom to ensure text contrast */
  background: linear-gradient(180deg,
      rgba(0, 0, 0, 0.85) 0%,
      rgba(0, 0, 0, 0.7) 32%,
      rgba(0, 0, 0, 0.45) 52%,
      rgba(255, 255, 255, 0.96) 75%,
      #ffffff 100%);
}

.onboarding-container {
  width: 100%;
  max-width: 400px;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: relative;
  padding: 20px;
}

.skip-button {
  position: absolute;
  top: 50px;
  right: 20px;
  background: rgba(0, 0, 0, 0.35);
  border: 2px solid #ffffffaa;
  color: #fff;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.3s ease;
  z-index: 10;
}

.skip-button:hover {
  background: rgba(255, 255, 255, 0.2);
  border-color: #fff;
}

.onboarding-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 20px 0;
}

.onboarding-text {
  max-width: 350px;
}

.onboarding-title {
  font-family: 'Akaya Telivigala', cursive;
  font-size: 2.2rem;
  font-weight: 800;
  margin: 0 0 10px 0;
  color: #fff;
  letter-spacing: -0.5px;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.onboarding-subtitle {
  font-family: 'Akaya Telivigala', cursive;
  font-size: 1.3rem;
  font-weight: 600;
  margin: 0 0 20px 0;
  color: #fff;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}

.onboarding-description {
  font-family: 'SF Pro Text', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', system-ui, sans-serif;
  font-size: 1rem;
  font-weight: 600;
  line-height: 1.6;
  color: #111;
  margin: 0 0 30px 0;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
}

/* Positioning helpers to match mockups */
.onboarding-text.top {
  margin-top: 80px;
}

.onboarding-text.bottom {
  margin-top: auto;
  background: transparent;
}

.onboarding-navigation {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  padding-bottom: 40px;
}

.onboarding-navigation-buttons {
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 20px 0 40px;
}

.progress-indicators {
  display: flex;
  gap: 12px;
  align-items: center;
}

.progress-dot {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background: #c7d2fe;
  /* light indigo like img 1 */
  transition: all 0.3s ease;
}

.progress-dot.active {
  /* blue ring with inner blue dot and subtle glow */
  background:
    radial-gradient(circle, #3D70FF 43%, transparent 44%),
    /* inner dot */
    #ffffff;
  /* center base */
  border: 2px solid #3D70FF;
  box-shadow: 0 0 0 3px rgba(61, 112, 255, 0.25);
  transform: scale(1.0);
}

.progress-dot.completed {
  background: #cbd5e1;
}

.navigation-buttons {
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 20px 0 40px;
}

.nav-button {
  width: 64px;
  height: 64px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.25s ease;
  padding: 0;
}

.prev-button {
  background: transparent;
  color: #111;
  border: 2px solid var(--theme-primary);
}

.prev-button:hover {
  background: rgba(61, 112, 255, 0.1);
  border-color: var(--theme-primary);
}

.next-button {
  background: var(--theme-primary);
  color: #fff;
  box-shadow: 0 8px 20px rgba(61, 112, 255, 0.35);
}

.next-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 24px rgba(61, 112, 255, 0.45);
}

.get-started-button {
  background: var(--theme-primary);
  color: #fff;
  border: none;
  border-radius: 12px;
  padding: 16px 32px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.25s ease;
  box-shadow: 0 8px 20px rgba(61, 112, 255, 0.35);
  min-width: 200px;
}

.get-started-button:active {
  transform: scale(0.98);
}

.get-started-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 24px rgba(61, 112, 255, 0.45);
}

/* Language Button - Bottom Center */
.language-button {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.35);
  border: 2px solid #ffffffaa;
  color: white;
  padding: 8px 14px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  z-index: 10;
  white-space: nowrap;
}

.language-button:hover {
  background: rgba(255, 255, 255, 0.2);
  border-color: #fff;
}

.language-button:active {
  transform: translateX(-50%) scale(0.98);
}

/* Language Dialog Overlay */
.language-dialog-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(5px);
}

.language-dialog {
  background: white;
  border-radius: 16px;
  width: 90%;
  max-width: 320px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  animation: slideUp 0.3s ease;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.language-dialog-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #f0f0f0;
}

.language-dialog-header h3 {
  margin: 0;
  color: #333;
  font-size: 18px;
  font-weight: 600;
}

.close-dialog {
  background: none;
  border: none;
  font-size: 24px;
  color: #999;
  cursor: pointer;
  padding: 0;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 0.2s ease;
}

.close-dialog:hover {
  background: #f5f5f5;
  color: #333;
}

.language-dialog-content {
  padding: 0;
}

.language-option {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 16px 20px;
  border: none;
  background: white;
  cursor: pointer;
  transition: all 0.2s ease;
  border-bottom: 1px solid #f8f8f8;
  gap: 12px;
}

.language-option:last-child {
  border-bottom: none;
}

.language-option:hover {
  background: #f8f8f8;
}

.language-option.active {
  background: #f0f8ff;
}

.language-flag {
  font-size: 20px;
}

.language-name {
  flex: 1;
  text-align: left;
  font-size: 16px;
  font-weight: 500;
  color: #333;
}

.language-check {
  color: #007AFF;
  font-size: 18px;
  font-weight: bold;
}

/* Animations */
@keyframes float {

  0%,
  100% {
    transform: translateY(0px);
  }

  50% {
    transform: translateY(-10px);
  }
}

@keyframes bounce {

  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }

  40% {
    transform: translateY(-10px);
  }

  60% {
    transform: translateY(-5px);
  }
}

/* Responsive Design */
.nav-icon {
  width: 24px;
  height: 24px;
  object-fit: contain;
  display: block;
  margin: 0 auto;
}

/* Make the arrow white when placed on the blue next button */
.next-button .nav-icon {
  filter: brightness(0) invert(1);
  width: 22px;
  height: 22px;
}

@media (max-width: 768px) {
  .onboarding-container {
    padding: 30px 15px;
  }

  .skip-button {
    top: 40px;
    right: 15px;
    font-size: 13px;
    padding: 6px 12px;
  }

  .onboarding-title {
    font-size: 1.8rem;
  }

  .onboarding-subtitle {
    font-size: 1.1rem;
  }

  .onboarding-description {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .onboarding-container {
    padding: 25px 10px;
  }

  .onboarding-title {
    font-size: 1.6rem;
  }

  .navigation-buttons {
    flex-direction: column;
    gap: 15px;
  }

  .nav-button {
    width: 100%;
    max-width: 200px;
  }
}